@font-face {
  font-family: "Main";
  src: url("./font/FONT.TTF") format("truetype")
}
a, a:hover {
  color: black;
  text-decoration: none;
}
a:hover {
  opacity: 0.7;
}
.section-margin {
  margin-top: 60px;
  margin-bottom: 60px;
}

footer {
  position: relative;
  left:0px;
  bottom:0px;
  height:50px;
  width:100%;
  background:#F8F9FA;
}
footer a {
  padding: 10px;
}

#faq {
  background-color: #F8F9FA;
}

header {
  font-family: 'Main', sans-serif;
}

html {
    font-size: 100%;
}

header {
  height: 100px;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Helvetica, Arial,
    sans-serif;
  font-size: 1rem;
  line-height: 1.5;
}

button:disabled {
  opacity: 0.5;
}

#homepage {
  text-align: center;
}

#homepage h1 {
  margin: 10% 0 20px 0;
  font-family: "Main";
}
small {
  display: block;
}
/* FOOTER */

.flex-center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.height-100 {
  height: 100%;
}

.context-dark, .bg-gray-dark, .bg-primary {
  color: rgba(255, 255, 255, 0.8);
}

.footer-classic a, .footer-classic a:focus, .footer-classic a:active {
  color: #ffffff;
}
.nav-list li {
  padding-top: 5px;
  padding-bottom: 5px;
}

.nav-list li a:hover:before {
  margin-left: 0;
  opacity: 1;
  visibility: visible;
}

ul, ol {
  list-style: none;
  padding: 0;
  margin: 0;
}

.h-two-fifty {
  height: 250px;
}
.w-two-fifty {
  width: 250px;
}

.bg-grey {
  background-color: #F8F9FA;
}

.social-inner {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  padding: 23px;
  font: 900 13px/1 "Lato", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
  text-transform: uppercase;
  color: rgba(255, 255, 255, 0.5);
}
.social-container .col {
  border: 1px solid rgba(255, 255, 255, 0.1);
}
.nav-list li a:before {
  content: "\f14f";
  font: 400 21px/1 "Material Design Icons";
  color: #4d6de6;
  display: inline-block;
  vertical-align: baseline;
  margin-left: -28px;
  margin-right: 7px;
  opacity: 0;
  visibility: hidden;
  transition: .22s ease;
}

#dashboardmodal {
  margin: 20px 0;
}

#terms {
  margin: 30px 0;
}

section {
  margin: 5% 0;
}

#download {
  margin: 20px;
  justify-content: center;
  align-content:center;
  align-items: center;

}

#download h1, h2, h3, a {
  font-family: 'main'
}